<template>
    <b-container fluid="lg" id="wraper">
        <!-- nav -->
        <ul class="nav nav-tabs">
            <li class="nav-item">
                <a class="nav-link" href="/">홈화면 <span class="sr-only">(current)</span></a>
            </li>
            <li class="nav-item">
                <a class="nav-link" href="#">일과기록</a>
            </li>
            <li class="nav-item">
                <a class="nav-link" href="/mari">마리상점</a>
            </li>
            <li class="nav-item">
                <a class="nav-link active" href="/collection">수집형 포인트</a>
            </li>
        </ul>

        <b-container>
            <div class="row">
                <div class="col-7">
                    <div class="tab-content" id="nav-tabContent">
                        <div class="tab-pane fade show active" id="list-home" role="tabpanel"
                            aria-labelledby="list-home-list">...</div>
                        <div class="tab-pane fade" id="list-profile" role="tabpanel"
                            aria-labelledby="list-profile-list">...</div>
                        <div class="tab-pane fade" id="list-messages" role="tabpanel"
                            aria-labelledby="list-messages-list">...</div>
                        <div class="tab-pane fade" id="list-settings" role="tabpanel"
                            aria-labelledby="list-settings-list">...</div>
                    </div>
                </div>
                <div class="col-5">
                    <div v-for="collection in collections" v-bind:key="collection.name" class="list-group" id="list-tab"
                        role="tablist">
                        <a class="list-group-item list-group-item-action" id="list-home-list" data-toggle="list"
                            href="#" role="tab" aria-controls="home">
                            {{collection.name}}
                            <input type="checkbox" v-model="collection.complete">
                            <!-- <span v-if="collection.complete">획득</span> -->
                            <!-- <span v-else>미획득</span></a> -->

                    </div>
                    <!-- <a class="list-group-item list-group-item-action" id="list-profile-list" data-toggle="list"
                            href="#list-profile" role="tab" aria-controls="profile">Profile</a>
                        <a class="list-group-item list-group-item-action" id="list-messages-list" data-toggle="list"
                            href="#list-messages" role="tab" aria-controls="messages">Messages</a>
                        <a class="list-group-item list-group-item-action" id="list-settings-list" data-toggle="list"
                            href="#list-settings" role="tab" aria-controls="settings">Settings</a> -->
                </div>
            </div>
            <!-- <ul v-for="collection in collections" v-bind:key="collection.name" class="list-group">
                <li>
                    {{collection.name}}
                </li>
            </ul> -->
        </b-container>


        <p style="color:white">{{collections}}</p>
    </b-container>
</template>

<script>
    export default {
        created() {
            // var id = '눈꽃츄르';
            // /%EB%88%88%EA%BD%83%EC%B8%84%EB%A5%B4
            this.$http.get('/api/collection')
                .then((response) => {
                    if (200 == response.status) {
                        var jsonObj = JSON.parse(response.data);
                        console.dir("jsonParse>>>>" + response.data);
                        for (var key in jsonObj) {
                            this.collections.push(jsonObj[key]);
                        }
                        console.dir("collection_f>>>>" + jsonObj);
                    } else {
                        console.log('response error : ' + response.status);
                    }
                });
        },
        data() {
            /*eslint-disable no-unused-labels*/
            return {
                collections: []
            }
        }
    }
</script>
<style lang="scss">
    @import '../assets/scss/nav';
    @import '../assets/scss/collection';
</style>